h1 {

  font-size: 60px !important;
  font-weight: 1000;
  line-height: 1;
  margin: 0 0 20px;
  font-family: 'Titillium Web', sans-serif;
}

h2 {
  font-size: 30px !important;

  font-size: 80px;
  font-weight: 1000;
  line-height: 2;
  opacity: 0.75;
  margin: 0 0 20px;
  font-family: 'Titillium Web', sans-serif;
}



.word-wrapper {
  white-space: nowrap;
}


.bold {
    font-weight:500;
}

.bold-name {
    font-weight: bold!important;
    font-size: 25px!important;
    
}

.avatar-style {
    margin: auto;
    border: solid 6px white;
}

.avatar-bkg {
    background-color:  #e9d8e1;
    padding-top: 60px;
    margin-top: -20px;
    border-radius: 6px;
    padding-bottom: 20px;
}

.chakra-ui-dark .avatar-bkg{
    background: #843d61;;
    color: black;
}

.modalContent {
    text-align: center;
    min-height: 60vh;
}

.chakra-modal__content-container {
    opacity: 0.95;
}
.card-bg-color{
    background: black;
    color: white;
}

.chakra-ui-dark .card-bg-color{
    background: #843d61;;

}

.cursor-card:hover{
    cursor: pointer;
}
